.about_intro_section {
  display: flex;
  flex-direction: column;
  color: white;
  position: relative;
  font-family: tbb;
  padding-left: 100px;
  width: 600px;
  height: 700px;
  min-height: 100vh;
  justify-content: center;
}
.about_intro_section aside {
  font-family: tbb;
  display: flex;
  flex-wrap: wrap;
  height: 48px;
  align-items: center;
}
.about_intro_section aside > h1 {
  position: relative;
  user-select: none;
  line-height: 48px;
  font-size: 48px;
  position: relative;
  z-index: 10;
  left: 10px;
}
.about_body_span {
  color: #42474b;
  opacity: .7;
  display: flex;
  cursor: default;
  user-select: none;
  position: relative;
  left: -15px;
  padding: 10px 0 10px 0;
}
.about_br_span {
  color: #42474b;
  opacity: .7;
  display: flex;
  cursor: default;
  user-select: none;
  position: relative;
  padding: 5px;
  padding-left: 0;
}
.about_p_span {
  color: #42474b;
  opacity: .7;
  display: flex;
  cursor: default;
  user-select: none;
  position: relative;
  padding: 5px 0 5px 0;
}
.about_h1_span {
  color: #42474b;
  opacity: .7;
  display: flex;
  cursor: default;
  user-select: none;
  position: relative;
  padding: 5px 0 5px 0;
}
.about_p {
  font-family: abel;
  font-size: 16px;
  position: relative;
  left: 15px;
  user-select: none;
}
.about_span {
  font-size: 16px;
}
.about_link {
  text-decoration: underline;
  color: #59bd8e;
}
.about_social_media {
  text-decoration: none;
  color: #59bd8e;
  padding-left: 5px;
}
.about_profile {
  width: 400px;
  height: 600px; 
  display: flex;
  align-self: center;
  position: relative;
  left: 5%;
  border-radius: 5px;
  box-shadow: 0px 0px 5px 2px black;
}
.about_profile_mobile {
  display: none;
}
@media (max-width:1000px) {
  .about_profile {
    display: none;
  }
  .about_profile_mobile {
    display: flex;
    height: 30px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px 1px #181818;
    position: relative;
    left: 15px;
  }
}
